html {
  font-size: 16px;
  color: #000;
}

body {
  font-family: 'Roboto', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
  background-color: #252525;
}

.wrap {
  min-width: 320px;
  max-width: 1200px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto;
}

.header {
  border-bottom: 4px solid #e5493a;
}

.header__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 35px 0;
}

.header__logo {
  margin-right: 20px;
}

.header__menu {
  padding: 0;
  margin: 0;
  margin-right: 15px;
}

.toggle__menu {
  font-family: "Raleway";
  font-size: 30px;
  color: white;
  display: none;
}

.toggle__menu img {
  width: 45px;
  height: 50px;
  margin-right: 10px;
}

.toggle__menu p {
  margin: 0;
  padding: 10px 0;
}

.toggle__menu:hover {
  cursor: pointer;
}

.header__menu {
  display: flex;
}

.menu {
  display: flex;
  list-style: none;
  flex-wrap: wrap;
}

.menu__element {
  margin-right: 35px;
}

.menu__element:last-child {
  margin-right: 0;
}

.menu__link {
  font-size: 14px;
  font-family: "Raleway";
  color: #707070;
  text-transform: uppercase;
  line-height: 2;
  text-decoration: none;
  display: inline-block;
  transition-duration: 0.5s;
  transition-property: color;
}

.menu__link::after {
  content: '';
  width: 8px;
  height: 2px;
  display: block;
  margin-bottom: 2px;
  opacity: 0;
  background-color: #e5493a;
  transition: 0.5s;
}

.menu__link:hover {
  color: #e5493a;
}

.menu__link:hover::after {
  width: 30px;
  opacity: 1;
}

.banner {
  background: url("../img/bg-man.jpg") center top no-repeat;
  background-size: cover;
}

.banner__wrap {
  padding-top: 255px;
  padding-bottom: 250px;
}

.banner_info {
  max-width: 580px;
  margin-left: auto;
}

.banner__title {
  color: #fff;
  font-size: 80px;
  font-weight: 800;
  font-family: 'Raleway';
  text-transform: uppercase;
  line-height: 1;
  margin: 0;
}

.banner__little {
  font-size: 43px;
  display: block;
}

.banner__red {
  color: orangered;
}

.banner__text {
  font-size: 18px;
  font-family: 'Raleway';
  font-weight: 400;
  color: #555555;
  text-transform: uppercase;
  line-height: 1.333;
  margin: 15px 0;
}

.bunner__button {
  border: 2px solid transparent;
  border-radius: 5px;
  background-color: #e5493a;
  width: 170px;
  height: 54px;
  font-size: 14px;
  font-family: 'Raleway';
  color: #fff;
  font-weight: 800;
  text-transform: uppercase;
  text-align: center;
  display: block;
  cursor: pointer;
  box-shadow: none;
  appearance: none;
  transition: 0.5s;
  display: inline-block;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-right: 25px;
}

.bunner__button:hover,
.bunner__button:active {
  background-color: #ba2719;
  border-color: #ba2719;
}

.bunner__button--last {
  margin-right: 0;
  border-color: #fff;
  background-color: transparent;
}

.advanteges__title {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

.advateges__list {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, auto));
  grid-column-gap: 30px;
  padding-top: 65px;
  padding-bottom: 50px;
}

.advateges__item {
  margin: 25px 0;
  transition: 0.5s all;
}

.advateges__item:hover .advateges__img {
  border-color: #e5493a;
}

.advateges__item:hover .advateges__svg {
  fill: #e5493a;
}

.advateges__item:hover .advateges__subtitle {
  color: #e5493a;
}

.advateges__item:hover .advateges__text {
  color: #fff;
}

.advateges__item:hover .advateges__text::after {
  width: 70px;
}

.advateges__img {
  border: 1px solid #363636;
  border-radius: 50%;
  position: relative;
  width: 104px;
  height: 104px;
  transition: 0.5s all;
}

.advateges__svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 40px;
  fill: #5f5e5e;
  transition: 0.5s all;
}

.advateges__subtitle {
  font-size: 18px;
  font-family: 'Raleway';
  color: #636363;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1.2;
  margin: 20px 0;
  transition: 0.5s all;
}

.advateges__text {
  font-size: 14px;
  font-family: 'Raleway';
  color: #636363;
  line-height: 1.5;
  margin: 20px 0;
  transition: 0.5s all;
}

.advateges__text::after {
  background-color: #e5493a;
  width: 30px;
  height: 2px;
  content: '';
  display: block;
  margin-top: 20px;
  transition: 1s all;
}

.works {
  background-color: #202020;
}

.works__title-hide {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

.works__list {
  padding-top: 80px;
  padding-bottom: 160px;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(12, minmax(60px, auto));
  grid-auto-rows: minmax(270px, auto);
}

.works__element-three {
  grid-column: span 3;
}

.works__element-four {
  grid-column: span 4;
}

.works__element-five {
  grid-column: span 5;
}

.works__element-six {
  grid-column: span 6;
}

.works__element-see-work:hover .works__title::before {
  background-color: yellow;
  width: 150px;
}

.works__title {
  font-size: 30px;
  font-family: 'Raleway';
  font-weight: bold;
  color: #fff;
  line-height: 1.1;
  text-transform: uppercase;
  margin: 25px 0;
  position: relative;
}

.works__title::before {
  background-color: #2f2f2f;
  position: absolute;
  width: 70px;
  height: 2px;
  left: 0;
  top: -25px;
  content: '';
  transition: 0.7s all;
}

.works__text {
  font-size: 16px;
  color: #464646;
  line-height: 1.5;
  text-align: left;
}

.works__element {
  position: relative;
}

.works__element:first-child {
  max-width: 270px;
}

.works__element:first-child::before {
  display: none;
}

.works__element::before {
  background-color: rgba(17, 17, 17, 0.85);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0;
  content: '';
  transition: 0.5s all;
}

.works__element:hover::before,
.works__element:active::before {
  z-index: 1;
  opacity: 1;
}

.works__element:hover .works__info,
.works__element:active .works__info {
  border: 5px solid #e5493a;
  opacity: 1;
  z-index: 2;
}

.works__element:hover .works__description::after,
.works__element:active .works__description::after {
  width: 150px;
  background-color: yellow;
}

.works__element:hover .works__zoom,
.works__element:active .works__zoom {
  background-color: yellowgreen;
}

.works__element:hover .works__link,
.works__element:active .works__link {
  background-color: #e5493a;
}

.works__element:nth-child(2) {
  background: url("../img/img1.jpg") center no-repeat;
  background-size: cover;
}

.works__element:nth-child(3) {
  background: url("../img/img2.jpg") center no-repeat;
  background-size: cover;
}

.works__element:nth-child(4) {
  background: url("../img/img3.jpg") center no-repeat;
  background-size: cover;
}

.works__element:nth-child(5) {
  background: url("../img/img4.jpg") center no-repeat;
  background-size: cover;
}

.works__element:nth-child(6) {
  background: url("../img/img5.jpg") center no-repeat;
  background-size: cover;
}

.works__info {
  width: 100%;
  height: 100%;
  position: relative;
  border: 5px solid rgba(255, 255, 255, 0);
  box-sizing: border-box;
  padding: 10px 25px 80px;
  z-index: 2;
  opacity: 0.2;
  transition: 1 all;
}

.works__name {
  font-size: 24px;
  font-family: 'Raleway';
  font-weight: bold;
  line-height: 1.1;
  color: #fff;
  text-transform: uppercase;
  margin: 20px 0;
}

.works__description {
  font-size: 12px;
  text-transform: uppercase;
  line-height: 1.7;
  color: #959595;
  margin: 20px 0;
  transition: 0.1s all;
}

.works__description::after {
  height: 2px;
  width: 50px;
  background-color: #e5493a;
  display: block;
  content: '';
  margin-top: 20px;
  transition: 1s all;
}

.works__zoom,
.works__link {
  border-radius: 50%;
  background-color: #252525;
  position: relative;
  cursor: pointer;
  display: inline-block;
  width: 42px;
  height: 42px;
  margin: 0 2px;
  appearance: none;
  padding: 0;
  border: none;
  transition: 0.5s all;
}

.works__zoom svg,
.works__link svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  fill: #fff;
  height: 15px;
  width: 15px;
}

.works__btns {
  position: absolute;
  bottom: 30px;
  right: 30px;
}

.works__element--active::before {
  z-index: 1;
  opacity: 1;
}

.works__element--active .works__info {
  border-color: #e5493a;
  opacity: 1;
  z-index: 2;
}

.gallery {
  background-image: radial-gradient(circle, #e5493a, #b8342f, #8c2023, #620e16, #3b0101);
}

.gallery__list {
  list-style: none;
  display: grid;
  padding-top: 80px;
  padding-bottom: 100px;
  grid-template-columns: repeat(10, minmax(80px, auto));
  grid-auto-rows: minmax(270px, auto);
  grid-column-gap: 1em;
}

.gallery__element-three {
  grid-column: span 3;
}

.gallery__element-four {
  grid-column: span 4;
}

.gallery__element-seven {
  grid-column: span 7;
}

.gallery__item {
  width: 100%;
  border: 1px solid #e5493a;
  border-radius: 5px;
}

.gallery__header {
  padding: 15px;
  font-size: 30px;
  color: #fff;
  margin: 0;
}

.gallery__header::after {
  background-color: #e5493a;
  width: 150px;
  height: 2px;
  content: '';
  display: block;
  margin-top: 20px;
  transition: 1s all;
}

.gallery__text {
  font-size: 14px;
  color: #000;
  margin: 15px;
}

.responsive__wrap {
  padding-top: 100px;
  padding-bottom: 100px;
  background: url("../img/bg-two.png") left bottom no-repeat;
}

.responsive__information {
  width: 360px;
  margin-left: auto;
}

.responsive__title {
  font-size: 30px;
  color: #fff;
  font-family: 'Raleway';
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1.1;
  margin: 20px 0;
  position: relative;
}

.responsive__title::before {
  width: 70px;
  height: 2px;
  background-color: #2f2f2f;
  position: absolute;
  left: 0;
  top: -20px;
  content: '';
}

.responsive__title span {
  color: #e5493a;
}

.responsive__text {
  font-size: 14px;
  line-height: 1.5;
  color: #464646;
  margin: 20px 0;
}

.responsive__list {
  margin: 20px 0;
}

.responsive__button {
  border: 2px solid transparent;
  border-radius: 5px;
  background-color: #8dc63f;
  width: 170px;
  height: 54px;
  font-size: 14px;
  font-family: 'Raleway';
  color: #fff;
  font-weight: 800;
  text-transform: uppercase;
  text-align: center;
  display: block;
  cursor: pointer;
  box-shadow: none;
  appearance: none;
  transition: 0.5s;
  margin-top: 50px;
}

.responsive__button:hover,
.responsive__button:active {
  background-color: #648e2a;
  border-color: #648e2a;
}

.list {
  font-size: 14px;
  line-height: 1.3;
  color: #959595;
  padding: 0;
  list-style: none;
}

.list__element {
  margin: 15px 0;
  padding-left: 55px;
  position: relative;
}

.list__element::before {
  content: '';
  height: 2px;
  width: 8px;
  position: absolute;
  background-color: #e5493a;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
}

.skills {
  background-color: orangered;
}

.skills__wrap {
  padding-top: 80px;
  padding-bottom: 80px;
  display: flex;
}

.skills__left {
  flex-basis: 280px;
  margin-right: 115px;
}

.skills__right {
  flex-grow: 1;
}

.skills__title {
  font-size: 30px;
  font-family: 'Raleway';
  color: #fff;
  font-weight: bold;
  margin: 20px 0;
  position: relative;
  text-transform: uppercase;
}

.skills__title::before {
  content: '';
  height: 2px;
  width: 70px;
  position: absolute;
  background-color: #fff;
  left: 0;
  top: -25px;
}

.skills__text {
  font-size: 14px;
  color: #fff;
  line-height: 1.5;
}

.skills__button {
  border: 2px solid transparent;
  border-radius: 5px;
  background-color: #252525;
  width: 170px;
  height: 54px;
  font-size: 14px;
  font-family: 'Raleway';
  color: #fff;
  font-weight: 800;
  text-transform: uppercase;
  text-align: center;
  display: block;
  cursor: pointer;
  box-shadow: none;
  appearance: none;
  transition: 0.5s;
  margin-top: 50px;
}

.skills__button:hover,
.skills__button:active {
  background-color: black;
  border-color: black;
}

.skills__left {
  margin-right: 50px;
  margin-bottom: 25px;
  flex-basis: 280px;
}

.skills__progress {
  margin: 20px 0;
}

.progress__name {
  font-size: 14px;
  font-family: 'Raleway';
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1.5;
  margin: 10px 0;
}

.progress__element {
  border: 1px solid rgba(255, 255, 255, 0.25);
  border-radius: 3px;
  padding: 2px;
}

.progress__line {
  background-color: #fff;
  width: 0%;
  height: 4px;
  position: relative;
}

.progress__tooltip {
  background-color: #252525;
  position: absolute;
  right: -15px;
  top: -32px;
  width: 31px;
  font-size: 11px;
  color: #fff;
  line-height: 1.9;
  font-weight: bold;
  text-align: center;
  border-radius: 3px;
